.hero {
  padding-top: 140px;
  position: relative;
  &__video {
    position: absolute;
    top: 50%;
    left: 12%;
    transform: translateY(-50%);
    z-index: -1;
    opacity: 0;
  }
  &__title {
    font-size: 340px;
    line-height: 439px;
    text-transform: uppercase;
    display: flex;
    margin: 0;
  }
  &__letters {
    &-left {
      transition: all 0.8s;
    }
    &-right {
      transition: all 0.8s;
    }
  }
  &__wrapper {
    margin-top: -111px;
    display: flex;
    justify-content: flex-end;
    margin-right: 57px;
  }
  &__descr {
    margin-top: 20px;
    font-size: 20px;
    line-height: 25px;
    max-width: 124px;
  }
  &__link {
    margin-top: -64px;
    padding-top: 140px;
  }
  &__present {
    font-size: 40px;
    line-height: 52px;
    color: var(--dark);
    background-color: var(--acid);
    margin-top: 140px;
    padding: 4px 20px;
    transition: all 0.3s;
    &:hover {
      background-color: var(--dark);
      color: var(--acid);
    }
  }
}
