.contacts {
   &__items {
      padding-top: 103px;
      display: flex;
      flex-direction: column;
      gap: 10px;
      position: sticky;
      top: 50%;
      transform: translateY(-50%);
   }
   &__wrapper {
      margin-top: 347px;
      position: relative;
   }
   &__item {
      display: flex;
      gap: 10px;
      position: relative;
   }
   &__key {
      font-family: 'TTFirs';
      font-size: 20px;
      line-height: 30px;
      color: var(--acid);
   }
   &__value {
      font-family: 'TTFirs';
      font-size: 20px;
      line-height: 30px;
      color: var(--grey);
      span {
         margin-left: -122px;
      }
      a {
         font-family: 'TTFirs';
         font-size: 20px;
         line-height: 30px;
         color: var(--grey);
         &:hover {
            text-decoration: none;
            color: var(--grey);
         }
      }
   }
   &__map {
      -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
      filter: grayscale(100%);
      position: relative;
      z-index: 5;
   }
   &__black {
      height: 100vh;
      width: 100%;
      background-color: var(--dark);
      position: absolute;
      top: 100px;
      left: 0;
      z-index: 2;
   }
}

.cont::-webkit-scrollbar {
   width: 0;
 }
