.works {
  position: relative;
  margin-top: -1500px;
  background-color: var(--dark);
  z-index: 10;
  &__titles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 160px;
  }
  &__title {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding-left: 152px;
    font-size: 120px;
    line-height: 155px;
    text-transform: uppercase;
    &-grey {
      background-color: var(--grey);
      color: var(--dark);
      overflow: hidden;
      z-index: 5;
    }
    &-dark {
      color: var(--grey);
      background-color: var(--dark);
      overflow: hidden;
      z-index: 2;
    }
  }
  &__wrapper {
    display: flex;
    justify-content: space-between;
    transition: all 1s;
    &:nth-child(2) {
      padding-top: 233px;
    }
    &:hover {
      margin-bottom: 88px;
      transition: all 1s;
      .works__img {
        visibility: visible;
        width: 30%;
      }
      .works__deskr {
        &-title {
          font-size: 80px;
          line-height: 90px;
          color: var(--acid);
        }
        &-subtitle {
          line-height: 10px;
        }
      }
    }
  }
  &__img {
    width: 0;
    height: 218px;
    visibility: hidden;
    transition: all 1s;
  }
  &__deskr {
    &-title {
      font-size: 40px;
      line-height: 50px;
      color: var(--grey);
      transition: all 1.2s;
    }
    &-subtitle {
      font-family: 'Arial';
      font-style: normal;
      font-weight: 400;
      font-size: 20px;
      line-height: 23px;
      color: var(--white);
      transition: all 1.2s;
    }
  }
}
