.request {
  position: relative;
  padding-top: 64px;
  padding-bottom: 118px;
  z-index: 1;
  overflow: hidden;
  &__title {
     font-family: 'TTFirs';
     font-weight: 500;
     font-size: 80px;
     line-height: 103px;
     color: var(--dark);
     margin-bottom: 110px;
  }
  &__circle {
    width: 100%;
    height: 120%;
    background-color: var(--grey);
    position: absolute;
    top: 0;
    left: 0;
    z-index: -5;
  }
  &__box {
   width: 100%;
   height: 50%;
   background-color: var(--grey);
   position: absolute;
   bottom: 0;
   left: 0;
   z-index: -4;
  }
  &__inp {
     margin-bottom: 23px;
     input {
        font-family: 'TTFirs';
        font-weight: 500;
        font-size: 40px;
        line-height: 52px;
        color: var(--dark);
        border: none;
        width: 53%;
        background-color: rgba($color: #000000, $alpha: 0);
        border-bottom: 2px solid var(--dark);
        text-transform: uppercase;
        transition: all 0.5s;
        outline: none;
        &::placeholder {
           color: var(--dark);
        }
     }
     textarea {
      width: 100%;
      font-size: 40px;
      line-height: 52px;
      color: var(--dark);
      background-color: rgba($color: #000000, $alpha: 0);
      border: none;
      border-bottom: 2px solid var(--dark);
      resize: vertical;
      &::placeholder {
        color: var(--dark);
      }
     }
     &-long {
        input {
           width: 100%;
        }
     }
  }
  &__btn {
     display: block;
     margin-left: auto;
     margin-top: 135px;
     background-color: rgba($color: #000000, $alpha: 0);
     border: none;
     font-family: 'TTFirs';
     font-weight: 500;
     font-size: 80px;
     line-height: 103px;
     color: var(--dark);
     cursor: pointer;
  }
}

.input__wrapper {
  position: relative;
  text-align: center;
  margin-top: 28px;
  transition: all 1s;
}

.input__file {
  opacity: 0;
  visibility: hidden;
  position: absolute;
}

.input__file-button-text {
  font-family: 'TTFirs';
  font-weight: 500;
  font-size: 40px;
  line-height: 52px;
  text-transform: uppercase;
  color: var(--dark);
  background-color: rgba($color: #000000, $alpha: 0);
  display: flex;
  align-items: center;
  justify-content: start;
  transition: all 0.5s;
  span {
     font-size: 80px;
     line-height: 103px;
     margin-right: 22px;
  }
}

.input__file-button {
  padding: 0px 5px;
  width: 53%;
  border: 2px solid var(--dark);
  background: rgba($color: #000000, $alpha: 0);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  cursor: pointer;
  transition: all 0.5s;
}

.custom-checkbox {
  position: absolute;
  z-index: -1;
  opacity: 0;

}

.custom-checkbox+label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  margin-top: 30px;
  font-family: 'TTFirs';
  font-weight: 500;
  font-size: 20px;
  line-height: 26px;
  color: var(--dark);
  a.policy {
     color: var(--dark);
     &:hover {
        text-decoration: none;
     }
  }
}

.custom-checkbox+label::before {
  content: '';
  display: inline-block;
  width: 60px;
  height: 60px;
  flex-shrink: 0;
  flex-grow: 0;
  border: 2px solid var(--dark);
  margin-right: 30px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
  transition: all 0.5s;
  cursor: pointer;
}

.custom-checkbox:checked+label::before {
  border-color: var(--dark);
  background-color: rgba($color: #000000, $alpha: 0);
  background-image: url("../img/svg/arrow4.svg");
  cursor: pointer;
}
