.footer {
   padding-top: 157px;
   padding-bottom: 100px;
   position: relative;
   background-color: var(--dark);
   z-index: 200;
   &__items {
      display: flex;
      align-items: center;
      justify-content: space-between;
      list-style: none;
      transition: opacity 5s;
      li {
         position: relative;
         transition: opacity 5s;
         &::after {
            content: "";
            width: 30px;
            height: 30px;
            border: 3px solid var(--acid);
            border-width: 3px 0 0 3px;
            transform: translate(-50%, -50%) rotate(45deg);
            position: absolute;
            left: 50%;
            top: 110%;
            opacity: 0;
         }
         &::before {
            content: "";
            width: 1px;
            height: 40px;
            border: 2px solid var(--acid);
            transform: translateX(-50%) rotate(0deg);
            position: absolute;
            left: 50%;
            top: 83%;
            opacity: 0;
         }
         &.arrow {
            &::after {
               opacity: 1;
            }
            &::before {
               opacity: 1;
            }
            a {
               font-weight: 500;
               font-size: 80px;
               line-height: 103px;
               color: var(--acid);
            }
         }
      }
   }
}
a.footer__item {
   text-decoration: none;
   font-family: 'TTFirs';
   font-weight: 500;
   font-size: 20px;
   line-height: 26px;
   text-transform: capitalize;
   color: var(--grey);
   position: relative;
   transition: opacity 5s, font-weight 1s, font-size 1s;
}

.arrow::after {
   animation: arrow1 1.5s infinite;
}

.arrow::before {
   animation: arrow2 1.5s infinite;
}

@keyframes arrow1 {
   from {
      top: 110%;
   }
   50% {
      top: 105%;
   }
   to {
      top: 110%;
   }
}

@keyframes arrow2 {
   from {
      top: 93%;
   }
   50% {
      top: 88%;
   }
   to {
      top: 93%;
   }
}
