.treading-line {
  background-color: var(--grey);
  padding-top: 120px;
  padding-bottom: 140px;
  &__line {
    font-size: 90px;
    line-height: 116px;
    text-transform: uppercase;
    color: var(--dark);
    white-space: nowrap;
    z-index: 1;
  }
  &__more {
    a {
      display: block;
      width: 509px;
      background-color: var(--dark);
      color: var(--grey);
      font-size: 40px;
      line-height: 40px;
      padding: 10px 17px 10px;
      margin-top: 87px;
      margin-left: auto;
      margin-right: auto;
      &:hover {
        color: var(--acid);
      }
    }
  }
  &__wrapper {
    position: relative;
    overflow: hidden;
  }
  &__block-top {
    position: absolute;
    width: 100%;
    height: 220px;
    top: 0;
    left: 0;
    background-color: var(--grey);
    z-index: 10;
  }
  &__block-bottom {
    position: absolute;
    width: 100%;
    height: 220px;
    bottom: 0;
    left: 0;
    background-color: var(--grey);
    z-index: 10;
  }
}
