.navigation {
   padding-top: 132px;
   &__list {
      display: flex;
      gap: 50px;
      align-items: center;
   }
   &__link {
      list-style: none;
      position: relative;
      a {
         white-space: nowrap;
         text-decoration: none;
         font-family: 'TTFirs';
         font-weight: 500;
         font-size: 20px;
         line-height: 26px;
         text-transform: capitalize;
         color: var(--grey);
         position: relative;
         transition: opacity 5s, font-weight 1s, font-size 1s;
      }
      &::after {
         content: "";
         width: 30px;
         height: 30px;
         border: 3px solid var(--acid);
         border-width: 3px 0 0 3px;
         transform: translate(-50%, -50%) rotate(45deg);
         position: absolute;
         left: 50%;
         top: 125%;
         opacity: 0;
      }
      &::before {
         content: "";
         width: 1px;
         height: 40px;
         border: 2px solid var(--acid);
         transform: translateX(-50%) rotate(0deg);
         position: absolute;
         left: 50%;
         top: 93%;
         opacity: 0;
      }
      &.arrow {
         &::after {
            opacity: 1;
         }
         &::before {
            opacity: 1;
         }
         a {
            font-size: 40px;
            line-height: 52px;
            color: var(--acid);
         }
      }
   }
}

.navigation__link.arrow::after {
   animation: arrow3 1.5s infinite;
}

.navigation__link.arrow::before {
   animation: arrow4 1.5s infinite;
}

@keyframes arrow3 {
   from {
      top: 140%;
   }
   50% {
      top: 135%;
   }
   to {
      top: 140%;
   }
}

@keyframes arrow4 {
   from {
      top: 103%;
   }
   50% {
      top: 98%;
   }
   to {
      top: 103%;
   }
}

.command {
   padding-top: 129px;
}

.conception {
   padding-top: 522px;
}

.title {
   font-family: 'TTFirs';
   font-size: 80px;
   line-height: 90px;
   text-transform: uppercase;
   color: var(--grey);
}

.ambition {
   padding-top: 748px;
   padding-bottom: 250px;
   overflow: hidden;
   &__cards {
      padding-top: 119px;
      display: grid;
      grid-template-columns: repeat(3, 500px);
      &-descr {
         margin-top: 50px;
         max-width: 342px;
         font-family: 'TTFirs';
         font-size: 20px;
         line-height: 25px;
         color: var(--dark);
      }
   }
   &__sin {
      height: 100vh;
      margin-top: 200px;
   }
   &__card1 {
      background: var(--acid);
      width: 500px;
      height: 500px;
      padding-top: 87px;
      padding-left: 49px;
      transform: rotate(5deg);
   }
   &__card2 {
      background: var(--grey);
      width: 500px;
      height: 500px;
      padding-top: 87px;
      padding-left: 49px;
      transform: translate(-25%, 50%);
      z-index: 2;
   }
   &__card3 {
      background: var(--white);
      width: 500px;
      height: 500px;
      padding-top: 87px;
      padding-left: 49px;
      transform: translateX(-35%) rotate(-5deg);
      z-index: 1;
   }
}

.valuess {
   padding-top: 99px;
   &__values {
      margin-top: 126px;
      font-family: 'TTFirs';
      font-size: 40px;
      line-height: 25px;
      color: var(--acid);
   }
}

.awards {
   padding-top: 153px;
   position: relative;
   &__img {
      position: absolute;
      top: 50px;
      left: 0;
      transform: translate(85%, -25%);
      z-index: -1;
   }
   &__items {
      padding-top: 53px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-template-areas: "it1 it2 it3"
                           "it4 it5 it3";
      align-items: center;
      gap: 33px;
   }
   &__item {
      display: flex;
      align-items: center;
      justify-content: space-between;
      &-img {
         max-width: 100%;
         min-height: 100%;
      }
      &-descr {
         font-family: 'Arial';
         font-style: normal;
         font-weight: 400;
         font-size: 40px;
         line-height: 25px;
         color: var(--acid);
      }
   }
}

.it1 {
   grid-area: it1;
}

.it2 {
   grid-area: it2;
}

.it3 {
   grid-area: it3;
}

.it4 {
   grid-area: it4;
}

.it5 {
   grid-area: it5;
}

.text {
   display: flex;
   justify-content: flex-end;
   position: relative;
   height: 1000px;
   &__title {
      position: sticky;
      top: 50%;
      transform: translateY(-50%);
      margin-top: 350px;
      margin-left: 264px;
      margin-right: -60px;
      font-family: 'TTFirs';
      font-size: 40px;
      line-height: 90px;
      color: var(--grey);
   }
}

.quest {
   font-family: 'TTFirs';
   font-size: 80px;
   line-height: 90px;
   color: var(--acid);
   &__title {
    line-height: 110px;
   }
}

.forms {
   margin-top: 107px;
   &__bg {
      padding: 130px 44px 131px 68px;
      background-color: var(--grey);
   }
   &__btn {
      background-color: var(--dark);
      color: var(--grey);
      margin-top: 20px;
      width: 100%;
      font-family: 'TTFirs';
      font-size: 40px;
      line-height: 52px;
      border: none;
   }
   &__inp {
      &:nth-child(4) {
         input {
            width: 100%;
            height: 125px;
            position: relative;
            word-break: break-word;
            &::placeholder {
               position: absolute;
               top: 0;
               left: 0;
               word-break: break-word;
            }
         }
      }
      input {
         font-family: 'TTFirs';
         font-size: 40px;
         line-height: 52px;
         border: none;
         border-bottom: 2px solid #000000;
         width: 583px;
         background-color: var(--grey);
         &::placeholder {
            color: var(--dark);
         }
      }
   }
}
