.header {
  padding-top: 97px;
  &__wrapper {
    display: flex;
    justify-content: flex-end;
  }
  &__menu {
    ul {
      display: flex;
      list-style: none;
    }
  }
  &__point {
    a {
      color: var(--white);
      font-size: 20px;
      line-height: 26px;
      text-transform: capitalize;
      &:hover {
        color: var(--acid);
      }
    }
  }
  &__logo {
    position: fixed;
    top: 97px;
    left: 160px;
    z-index: 1000;
  }
}
