.contacts {
   position: relative;
   padding-top: 140px;
   padding-bottom: 10px;
   z-index: 200;
   background-color: var(--dark);
   &__logos {
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 9px;
      position: relative;
   }
   &__logo {
      &-circle {
         height: 36px;
         width: 36px;
         border-radius: 50%;
         background-color: var(--acid);
         animation: circle 5s infinite;
         position: relative;
         z-index: 5;
         transition: all 0.5s;
         transform: translate(0) scale(0.5);
      }
      &-cube {
         height: 36px;
         width: 36px;
         background-color: var(--acid);
         border-radius: 0%;
         animation: cube 3s infinite;
         position: absolute;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         z-index: 1;
         &::before {
            content: "";
            height: 30px;
            width: 30px;
            border-radius: 50%;
            background-color: var(--dark);
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
         }
      }
   }
   &__phone {
      a {
         margin-top: 37px;
         display: block;
         align-items: center;
         font-family: 'Arial';
         font-weight: 400;
         font-size: 20px;
         line-height: 30px;
         text-align: center;
         color: var(--grey);
         &:hover {
            color: var(--acid);
         }
      }
   }
   &__mail {
      a {
         display: block;
         align-items: center;
         font-family: 'Arial';
         font-weight: 400;
         font-size: 20px;
         line-height: 30px;
         text-align: center;
         color: var(--grey);
         &:hover {
            color: var(--acid);
         }
      }
   }
   &__adr {
      align-items: center;
      font-family: 'Arial';
      font-weight: 400;
      font-size: 20px;
      line-height: 30px;
      text-align: center;
      color: var(--grey);
      a {
         display: block;
         align-items: center;
         font-family: 'Arial';
         font-weight: 400;
         font-size: 20px;
         line-height: 30px;
         text-align: center;
         color: var(--grey);
         &:hover {
            color: var(--acid);
         }
      }
   }
   &__socials {
      display: flex;
      justify-content: center;
      gap: 70px;
      margin-top: 34px;
   }
   &__social {
      a {
         font-family: 'TTFirs';
         font-weight: 500;
         font-size: 20px;
         line-height: 26px;
         text-transform: capitalize;
         color: var(--grey);
         position: relative;
         &:hover {
            text-decoration: none;
            transition: all 0.4s;
            &::before {
               content: "";
               width: 10px;
               height: 10px;
               border: 2px solid var(--grey);
               border-width: 2px 0 0 2px;
               transform: translateY(-50%) rotate(135deg);
               position: absolute;
               left: -20%;
               top: 50%;
            }
            &::after {
               content: "";
               width: 0;
               height: 1px;
               border: 1px solid var(--grey);
               background-color: var(--grey);
               transform: rotate(180deg);
               position: absolute;
               left: 0%;
               top: 100%;
               z-index: 200;
            }
         }
      }
   }
}

.contacts__social a:hover::after {
   animation: social 2.5s forwards;
}

@keyframes circle {
   from {
      transform: translateX(0) scale(0.75);
   }
   25% {
      transform: translateX(-45px);
   }
   50% {
      transform: translateX(0) scale(0.75);
   }
   75% {
      transform: translateX(45px);
   }
   to {
      transform: translateX(0) scale(0.75);
   }
}

@keyframes social {
   from {
      width: 1%;
   }
   to {
      width: 100%;
   }
}
