.filters {
  margin-top: 103px;
  &__wrapper {
    display: flex;
  }
  &__filtr {
    font-family: 'TTFirs';
    font-size: 20px;
    line-height: 20px;
    color: var(--grey);
    cursor: pointer;
    transition: all .5s;
    &.active {
      color: var(--acid);
    }
  }
  &__list {
    list-style-type: none;
    padding: 0;
    padding-left: 30px;
    margin: 0;
    width: 0;
    height: 0;
    overflow: hidden;
    transition: all 1.5s;
    &.active {
      height: 100%;
      width: 100%;
    }
  }
  &__link {
    a {
      font-family: 'TTFirs';
      font-size: 20px;
      line-height: 26px;
      color: var(--grey);
      position: relative;
      transition: all 1s;
      &:hover {
        color: var(--acid);
        &::before {
          content: ">";
          color: var(--acid);
          margin-right: 10px;
        }
      }
    }
  }
}

.cas {
  margin-top: 67px;
  position: relative;
  z-index: 205;
  &__wrapper {
    display: flex;
  }
  &__left {
    width: 50%;
  }
  &__right {
    width: 50%;
  }
  &__pink {
    height: 453px;
    background-color: var(--pink);
  }
  &__grey {
    height: 556px;
    background-color: var(--grey);
  }
}
