.contractors {
   padding-bottom: 100px;
   &__bg {
      margin-top: 135px;
      background-color: var(--grey);
   }
   &__items {
      list-style: none;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 2px solid var(--dark);
      &:hover {
         background-color: var(--dark);
         .contractors__item {
            color: var(--grey);
            a {
               display: block;
            }
         }
      }
   }
   &__item {
      font-family: 'TTFirs';
      font-size: 40px;
      line-height: 50px;
      text-transform: uppercase;
      color: var(--dark);
      a {
         font-family: 'Arial';
         font-style: normal;
         font-weight: 400;
         font-size: 20px;
         line-height: 30px;
         color: var(--acid);
         margin-right: 44px;
         display: none;
         &:hover {
            text-decoration: none;
         }
      }
   }
}
