.circles {
  position: relative;
  height: 3000px;
  z-index: 5;
  &__circle {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 0;
    width: 0;
    border-radius: 50%;
    background-color: var(--grey);
  }
  &__wrapper {
    position: absolute;
    top: 30%;
    left: 50%;
    transform: translate(-50%, -40%);
    width: 100%;

  }
  &__text {
    font-size: 80px;
    line-height: 90px;
    color: var(--dark);
    position: relative;
    z-index: 2;
    text-align: center;
  }
  &__cube {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: var(--grey);
    z-index: 3;
  }
}
