.titles {
  &__titles {
    margin-top: 50vh;
    font-family: 'TTFirs';
    font-size: 120px;
    line-height: 155px;
    text-transform: uppercase;
    color: var(--acid);
    white-space: nowrap;
    will-change: transform;
  }
}

.scroll-element {
   position: absolute;
   top: 25%;
   transform: translate(-50%, -50%);
   left: 50%;
}

.services {
  margin-top: 95px;
  &__btn {
   position: absolute;
   top: 0;
   left: 4571px;
   transform: translateY(-50%);
   padding: 2px 10px;
   font-family: 'TTFirs';
   font-size: 40px;
   line-height: 52px;
   color: #000000;
   background-color: var(--acid);
   cursor: pointer;
   transition: all 0.3s;
   &:hover {
      color: var(--acid);
      background-color: var(--dark);
   }
  }
  &__items {
    display: flex;
  }
  &__item {
    font-family: 'TTFirs';
    font-size: 80px;
    line-height: 103px;
    text-transform: uppercase;
    color: var(--grey);
    position: relative;
    &-1 {
      &:after {
        position: absolute;
        bottom: -15px;
        left: 50%;
        transform: translateX(-50%);
        content: "";
        width: 26px;
        height: 26px;
        border-radius: 50%;
        background-color: var(--grey);
      }
    }
    &-2 {
      &:after {
        position: absolute;
        bottom: -15px;
        left: 50%;
