    }
    &:nth-child(3) {
      margin-right: 371px;
    }
    &:nth-child(4) {
      margin-right: 353px;
    }
    &:nth-child(5) {
      margin-right: 360px;
    }
  }
  &__lines {
    position: relative;
  }
  &__line {
    &-grey {
      width: 4701px;
      height: 4px;
      background-color: var(--grey);
      border: none;
      margin: 0;
      transform: translateX(-160px);
    }
    &-acid {
      margin: 0;
      transform: translateX(-160px);
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 4px;
      background-color: var(--acid);
      border: none;
    }
  }
  &__wrapper {
    display: flex;
    margin-top: 23px;
  }
  &__text {
    font-family: 'TTFirs';
    font-size: 20px;
    line-height: 25px;
    color: #FFFFFF;
    white-space: nowrap;
    &-1 {
      display: block;
      overflow: hidden;
      color: var(--acid);
      height: 0px;
      transition: all 1s;
      padding: 10px 0;
    }
    &-2 {
      display: block;
