.cases {
  margin-top: 352px;
  padding-bottom: 137px;
  &__wrapper {
    &:nth-child(2) {
      margin-top: 500px;
    }
    &:nth-child(3) {
      margin-top: 500px;
    }
    &:nth-child(4) {
      margin-top: 500px;
    }
  }
  &__cube {
    &-vertical-right {
      width: 450px;
      height: 550px;
      background-color: var(--acid);
      margin-left: 350px;
      position: relative;
      &:hover {
         z-index: 10;
      }
    }
    &-horizontal-left {
      width: 550px;
      height: 450px;
      background-color: var(--acid);
      margin-left: 70px;
      position: relative;
      &:hover {
         z-index: 10;
      }
    }
    &-vertical-left {
      width: 450px;
      height: 550px;
      background-color: var(--acid);
      margin-left: 182px;
      position: relative;
      &:hover {
         z-index: 10;
      }
    }
    &-horizontal-right {
      width: 550px;
      height: 450px;
      background-color: var(--acid);
      margin-left: 383px;
      position: relative;
      &:hover {
         z-index: 10;
      }
    }
  }
  &__img {
    &-vertical-right {
      img {
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
        -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);
        transition: all 0.3s;
        mix-blend-mode: luminosity;
        &:hover {
          -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
          filter: grayscale(0%);
          mix-blend-mode: normal;
          transform: scale(1.1);
          &.cases__cube-vertical-right {
            z-index: 10;
          }
        }
      }
    }
    &-horizontal-left {
      img {
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
        -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);
        transition: all 0.3s;
        mix-blend-mode: luminosity;
        &:hover {
          -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
          filter: grayscale(0%);
          mix-blend-mode: normal;
          transform: scale(1.1);
        }
      }
    }
    &-vertical-left {
      img {
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
        -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);
        transition: all 0.3s;
        mix-blend-mode: luminosity;
        &:hover {
          -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
          filter: grayscale(0%);
          mix-blend-mode: normal;
          transform: scale(1.1);
        }
      }
    }
    &-horizontal-right {
      img {
        object-fit: cover;
        object-position: center;
        width: 100%;
        height: 100%;
        -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
        filter: grayscale(100%);
        transition: all 0.3s;
        mix-blend-mode: luminosity;
        &:hover {
          -webkit-filter: grayscale(0%); /* Safari 6.0 - 9.0 */
          filter: grayscale(0%);
          mix-blend-mode: normal;
          transform: scale(1.1);
        }
      }
    }
  }
  &__descr {
    font-size: 20px;
    line-height: 25px;
    color: var(--white);
    max-width: 270px;
    &-vertical-right {
      margin-top: -50px;
      margin-left: auto;
      margin-right: 50px;
    }
    &-vertical-left {
      margin-left: auto;
      margin-right: 15px;
    }
  }
  &__latter-vertical-right {
    display: inline-block;
    font-size: 380px;
    line-height: 490px;
    text-transform: uppercase;
    color: var(--grey);
    &-first {
      position: relative;
      z-index: -1;
    }
    &-second {
      position: relative;
      z-index: 5;
    }
  }
  &__latter-horizontal-left {
    display: inline-block;
    font-size: 380px;
    line-height: 490px;
    text-transform: uppercase;
    color: var(--grey);
    &-first {
      position: relative;
      z-index: 5;
    }
    &-second {
      position: relative;
      z-index: -1;
    }
  }
  &__latter-vertical-left {
    display: inline-block;
    font-size: 380px;
    line-height: 490px;
    text-transform: uppercase;
    color: var(--grey);
    &-first {
      position: relative;
      z-index: 5;
    }
    &-second {
      position: relative;
      z-index: -1;
    }
  }
  &__latter-horizontal-right {
    display: inline-block;
    font-size: 380px;
    line-height: 490px;
    text-transform: uppercase;
    color: var(--grey);
    &-first {
      position: relative;
      z-index: -1;
    }
    &-second {
      position: relative;
      z-index: 5;
    }
  }
  &__projects {
    a {
      display: block;
      width: 280px;
      font-size: 40px;
      line-height: 52px;
      color: var(--dark);
      background-color: var(--acid);
      padding: 2px 13px 3px 17px;
      margin-top: 135px;
      margin-left: auto;
      margin-right: 10px;
      transition: all 0.5s;
      &:hover {
        background-color: var(--dark);
        color: var(--acid);
      }
    }
  }
}
